import React from "react"
import ArticlePreface from "./ArticlePreface"
import ArticleSection from "./ArticleSection"
import BlankLink from "./BlankLink"
import ArticleImage from "./ArticleImage"

const Article1 = () => (
  <div>
    <ArticleImage src="/images/1.gif" />
    <ArticlePreface>
      You can click the folling link to interact with a Facebook Messenger implementation of <i>Stanley the Chatbot</i>:
      <br />
      <BlankLink href="https://www.messenger.com/t/letthetalenttalk/">
        https://www.messenger.com/t/letthetalenttalk/
      </BlankLink>
      <br />
      This implementation helps Talkpush (the company that creates <i>Stanley the Bot</i>) recruit talents. The chatbot have been updated since I left the project, but its core features still remain the same.
      <br /><br />
      Official Introductory Page:<br />
      <BlankLink href="https://talkpush.com/chatbot.html">
        https://talkpush.com/chatbot.html
      </BlankLink>

      <br /><br />
      Responsibilities:
      <ul>
        <li>Developing the chatbot service</li>
        <li>Developing relevant user interfaces for clients or system administrators to setup and customize the service with ease</li>
        <li>Participate in the design process</li>
      </ul>
      (I'm the major software engineer in this project. Other major team members include a product manager and a senior software engineer whom I consulted with)

      <br/><br />
      Major Skills:
      <br />
      Ruby, Rails, PostgreSQL, Facebook for Developers, WeChat Open Platform, Twilio, Amazon Web Services, Ubuntu (Linux), React, webpack, ES6 (JavaScript), CSS, HTML
    </ArticlePreface>

    <ArticleSection subhead="The Problem">
      <p>
        For organizations that <strong>interview high volumes of candidates</strong> such as call centers, hotels and business process outsourcing companies, <strong>arranging first-round interviews and conduct screening</strong> is a real pain. Scheduling and conducting interviews and managing information generated during interviews are typical processes that soak up a great deal of time and resources. Conventionally, phone interview is thought to be an comparatively desirable option, even with which a large portion of HR personnel is allocated to make hundreds of phone calls asking the same questions repeatedly while taking notes in a rush.
      </p>
      <ArticleImage src="/images/1-1.jpg" />
    </ArticleSection>

    <ArticleSection subhead="The Solution">
      <p>
        <BlankLink href="http://talkpush.com/"><i>Talkpush</i></BlankLink> has been dedicated to addressing this problem by means of <strong>information technology</strong>. Our client would spend a little time setting up recruitment campaigns in our system and specify what to ask for each campaign. Then just as they used to, they post their job openings onto different channels, typically job board websites, but this time instructing applicants to send resumes to Talkpush or complete a form with tools such as Google Form. Next, <strong>our software would condust interviews and "taking notes" (generating records into the system)</strong> on behalf of them. When the recruiters come back a few days later, usually there would be hundreds candidate records with extracted resume information and recorded interview answers for them to screen and they can manage candidates, e.g. shortlisting a candidate and send related e-mail to him, <strong>with our easy-to-use user interface</strong>.
      </p>
      <p>
        When the company was still based in Hong Kong, I joined the team and worked as the major developer of <i>Stanley the Bot</i>, before which our automated interview solution was mainly based on phone calls, which however still left much to be desired. Apart from cost, end-user experience and reliability were also things Talkpush had been trying to improve. You know what it is like to interact with an automated phone call: spend a long time listening to instructions, input numbers or talk and then start to listen again. It wasn't quite <strong>efficient and pleasent</strong> for end-users to interact with. And sometimes unstable <strong>call quality</strong> would result in choppy, obscure or even totally blank voice answers, which candidates who record them weren't even aware of.
      </p>
      <p>
        Around June 2016, when I just joined the team, Facebook lauched their <BlankLink href="https://developers.facebook.com/docs/messenger-platform"><strong>Messenger platform</strong></BlankLink>, so we decided to give it a try since instant messaging could potentially provide much better user experience and reliability compared to phone calls. The platform inspired the idead of <i>Stanley the Chatbot</i>. Typically when people apply for jobs or attend their first-round interviews, they have to interact with different platforms including job boards, e-mails, online forms and phone calls and there can be all kinds of mistakes they might make through the whole process. We wanted to make the chatbot a <strong>one-stop service</strong> where job applicants can accomplish multiple tasks such as discovering job positions, inputting personal information, attending interviews and getting updates about their applications. What's more, we were eager to create a <strong>conversational experience</strong> with it, where job applicants were guided through the whole process.
      </p>
    </ArticleSection>

    <ArticleSection subhead="Software Development">
      <p>
        My start point was Facebook's <BlankLink href="https://developers.facebook.com/docs/messenger-platform"><strong>Messenger Platform documentation</strong></BlankLink>. It documents <strong><BlankLink href="https://developers.facebook.com/docs/messenger-platform/webhook#format">what kind of data</BlankLink> you will receive when users ineract with Messenger</strong>, <strong><BlankLink href="https://developers.facebook.com/docs/messenger-platform/introduction/conversation-components">a set of conversation components</BlankLink> which you can utilize to present information</strong> and <strong>how you can instruct Messenger to display an instance of a component as you wish</strong>.
      </p>
      <p>
        The first thing I needed to figure out how to simply receive information about interaction from the Messenger side and react to it. I first created a Facebook App at <BlankLink href="https://developers.facebook.com">Facebook for Developers</BlankLink> and bound a specific <BlankLink href="https://www.facebook.com/business/products/pages">Facebook Page</BlankLink> to the App. Then I created an endpoint in our Rails application and provide the address of the enpoint in the Facebook App. To put it simple, now the Facebook Page and our application were connected through the Facebook App. So, whenever a <strong>user interacted</strong> with the specified Facebook Page, Facebook would deliver data to us describing the event, essentially telling us <strong>who</strong> interacted with the page and <strong>how</strong>. Then you could make a <strong>POST request</strong> with payload describing <strong>whom to reply to</strong> and <strong>what to reply</strong>, conforming to Facebook's <BlankLink href="https://developers.facebook.com/docs/messenger-platform/reference/send-api">Send API</BlankLink>. After Facebook receive it, the Messenger of that page would reply according to your instruction.
      </p>
      <p>
        Then it comes the more complicated part: to create the actual chatbot service to <strong>implement the business logic</strong>. According to the guideline our product manager provided, this is how a user should interact with the chatbot (to make it more simple, the following diagram will only be focused on the uesr's operations and won't go into details):
      </p>
      <ArticleImage src="/images/1-2.png" />
      <p>
        I started to develope it with the excellent object-oriented programming language, <BlankLink href="https://www.ruby-lang.org/">Ruby</BlankLink>. The Chatbot is essentially a <strong>Ruby class</strong>. For each event which Facebook informed us of, we initialized an instance of the Ruby class to handle it.
      </p>
      <p>
        To design the whole system, I started to break down the problem and build the essential parts of the chatbot. When you interact with an ATM machine, you know that there are different stages of the whole interaction. The ATM machine handles your inputs differently based on which stage your are in (e.g., it can treat your input of numbers as password or amount of money) and move to another stage based on your input. Behind this is usually what we called <strong>finite-state machine</strong>. As our chatbot works in a similar way, I also integrated it with state machine, where I specified what were the <strong>possible states</strong> and <strong>transitions for a certain state</strong>. Then I started to build other parts around the state machine so that the chatbot can take the user input (text, audio, video, etc.) and <strong>determine the next state transition</strong>, <strong>perform database operations</strong>, <strong>generate the content to reply</strong> accordingly and <strong>send the instruction</strong> to Facebook. To make the chatbot reply more quickly, I made as many as possible database operations asynchronous so that the chatbot <strong>won't have to wait until they were completed</strong> before they got down to other things.
      </p>
      <p>
        Apart from job applicants, end-users who interact directly with the chatbot, our clients who utilized the chatbot to hire were also users of the chatbot. Because our clients were very likely to have <strong>different requirements</strong> for the chatbot, after developing a working demo, I proposed to refactor the chatbot to enable customization. To achieve this, I extracted the different parts of the chatbot into seperate Ruby classes. Then I was able to create different feature groups where some of the essential parts of the chatbot got inherited and then overrided. This was where I utilized the power of <strong>OOP (object-oriented programming)</strong>. At last, I created a user interface where our clients can customize their own chatbot, appending or removing features, just with <strong>a few clicks on some checkboxes</strong>. For example, if a client wanted to recruit in multiple coutries and needed the chatbot to ask for the country code of each candidate's phone number, they could just check the related checkbox. In addition, if a client had special requirements which our existing feature modules couldn't satisfy, there was a more flexible option: they could specify their requirements and we could develop a feature just for them in a few hours. With the OOP powered design of the system, we only needed to write minimal amount of code to implement variations.
      </p>
    </ArticleSection>

    <ArticleSection subhead="Design">
      <p>
        Since our team maintained a flat structure and flexible ethos, I was also actively involved in the design process. Here are some of the improvements I contributed.
      </p>
      <p>
        When a user interacted with the chatbot, sometimes it might took our system to process the information especially when it needed to perform database operations. And if users didn't know what was going on in these couple of seconds, some of them tended to send <strong>extra messages</strong> which might result in <strong>extra requests</strong> and even <strong>errors</strong>. Though I tried my best to make the reply process more fast, it was <strong>displaying the status of the chatbot</strong> that solved the problem more effectively. When you chat with a person on a instant messaging app, most apps will inform you of the status of the other person, for example, whether he is responding or whether he has read your message. And the Messenger Platform allows developers to achieve something similar with <BlankLink href="https://developers.facebook.com/docs/messenger-platform/send-messages/sender-actions">Sender Actions</BlankLink>, which I implemented with the chatbot so that as soon as it was informed of a user's input, it would immediately instruct Messenger to display "mark_seen" and "typing_on" telling the user that our system had received his input and was processing it. This change greatly reduced extra inputs, teaching me how seemingly insignificant elements can make a big impact on users' behaviors.
      </p>
      <p>
        Because both we and our users could make mistakes, I also enable the chatbot to <strong>recover and abdicate</strong>.I reserved some words such as "reset" and "restart", which if a user inputted, the chatbot would be reset to the first state. Besides, I added a "restart" button to the <BlankLink href="https://developers.facebook.com/docs/messenger-platform/send-messages/persistent-menu">Persistent Menu</BlankLink> to achieve this. However, some of the users might not try these options spontaneously. So, if they didn't interact with the chatbot as he was supposed to, for example, asking questions while he was required to provide his information, he would be informed how to reset or disable the chatbot. If the mistake continued to be made, the chatbot would abdicate by itself, allowing a human to communicate with the user to help.
      </p>
      <p>
        An important lesson I learnt from this project was that it is crucial <strong>not to underestimate how differently they might interact with the product</strong>. In our eyes, a design might seem simple, because we had proior knowledge about the product. However, it can be complex and difficult to interact with in a user's perspective. Interacting with our chatbot, <strong>different users had different problems</strong>: some users ignored our hint and interacted with the chatbot as if it was an actual human; some users didn't know what coutry code meant exactly, inputting abbreviation of their country instead; some users, who lack experience with IM apps, didn't even know how to record voice with Messenger. It is by <strong>empathizing with users</strong> and <strong>reaching out to them to understand thier needs better</strong> that we can effectively improve the product to satisfy their needs.
      </p>
    </ArticleSection>
  </div>
)

export default Article1



