import React from "react"
import ArticleImage from "./ArticleImage"
import { Link } from "react-router-dom"

const Article6 = () => (
  <div>
    <ArticleImage src="/images/6.gif" />
    <p className="article-preface">
      Responsibilities:
      <br />
      Designing and developing the application solely
      <br/><br />
      <strong>Major Skills:</strong>
      <br />
      React, React Router, webpack, ES6 (JavaScript), CSS, HTML, GitHub Pages, Git
      <br /><br />
      Source Code: <a href="https://github.com/stanleysq/stanleysq-web" target="_blank" rel="noopener noreferrer">https://github.com/stanleysq/stanleysq-web</a>
    </p>

    <p className="article-subhead">
      Design
    </p>
    <p>
      In designing the website, I tried to make some improvements according to the issues I noticed when visiting others' personal websites.
    </p>
    <p>
      I'm aware that the interests and needs vary among the users who visit my personal website. Some people might be really interested in one's projects and want to read about details while others just want to take a glance. However, when presenting the portfolio index which contain all the works, many personal websites only display thumbnails and titles of the projects, making it hard to get a basic idea about some of the projects. So, when designing my portfolio index, I put my screenshots into device frames to indicate what is the major platform the project was based on and I added a short description beneath each title.
    </p>
    <ArticleImage src="/images/6-1.png" />
    <p>
      To get a little bit more information such as my responsibilities in the project and skills involved, you can simply read the gray preface text at the beginning of each project:
    </p>
    <ArticleImage src="/images/6-2.png" />
    <p>
      And of course, if you are really interested, you can go through the whole article to get more details. When you finish reading, you will see the portfolio index again at the bottom, this time without the project you are reading about. Oftentimes, when I visit others' websites, I have to navigate back and forth in order to view serveral articles, which is quite frustrating and is what I'm trying to address here.
    </p>
    <ArticleImage src="/images/6-3.png" />
    <p className="article-subhead">
      Software Development
    </p>
    <p>
      Similar to <Link to='/projects/5'>my playground app</Link>, this app is developed with React and client-side routing is implemented so that a user can navigate the website smoothly without built-in browser features, such as bookmarking, being compromised.
    </p>
  </div>
)

export default Article6



