import React from "react"
import { withRouter } from "react-router"
import { Link } from "react-router-dom"
import FontAwesome from "react-fontawesome"
import "./Home.css"

class Home extends React.PureComponent {
  componentDidMount() { global.objectFitPolyfill() }

  render() {
    return(
      <div className="home-wrapper">
        <video className="home-video" data-object-fit autoPlay loop src="http://a1.phobos.apple.com/us/r1000/000/Features/atv/AutumnResources/videos/comp_C002_C005_0818SC_001_v01_6M_HB_tag0.mov">
        </video>
        <div className="home-image" />
        <div className="overlay">
          <div className="home-text-box">
            <div>
              <img className="home-avatar" alt="avatar" src="/images/avatar.png" />
            </div>
            <h1 className="home-header serif">Hi, I'm Stanley Sun</h1>
            <p>
              I'm a full-stack developer based in China. I'm enthusiastic about developing interactive products which address people's needs so as to improve efficiency, spread knowledge and catalyze real joy. By far, I've worked on a variety of exiting projects where various technologies and platforms are involved. You can <Link to="/projects">click here</Link> to check out these projects.
            </p>
            <p>
              Besides, I'm also a cinephile who enjoys films from all over the world. Chinese martial arts film, Japanese homin-geki, French New Wave... To me, cinema is not only a place to have fun, but also a place where I learn about the diversity, complexity and beauty of the world as well as all kinds of fantastic ways to depict them and express ideas. If you're interested, <a href="http://45.248.87.15/en-us/lists/2/divisions/1/" target="_blank" rel="noopener noreferrer">here</a> are some of my favourites .
            </p>
            <p>
              <FontAwesome name="github" /> Github:&nbsp;
              <a
                href="https://github.com/stanleysq/"
                target="_blank" rel="noopener noreferrer"
              >
                stanleysq
              </a>&nbsp;&nbsp;
              <FontAwesome name="stack-exchange" /> Stack Exchange:&nbsp;
              <a
                href="https://stackexchange.com/users/5691525/sunquan?tab=activity"
                target="_blank" rel="noopener noreferrer"
              >
                sunquan
              </a>&nbsp;&nbsp;
              <FontAwesome name="envelope" /> Email:&nbsp;
              <a href="mailto://stanley.sq@outlook.com/">
                stanley.sq@yahoo.com
              </a>&nbsp;&nbsp;
            </p>
            <p className="home-footnote">Background Video: Apple TV Screensaver - China</p>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(Home)



