import React from "react"
import ArticleImage from "./ArticleImage"

const Article3 = () => (
  <div>
    <ArticleImage src="/images/3-1.png" />
    <p className="article-preface">
      Check out the following video to see how the solution benefit one of our clients, <i>House of Blue Jeans</i> in Netherlands, and how people interact with the products.
      <iframe title="omnitech" width="560" height="315" className="article-video"  src="https://www.youtube.com/embed/XzjMRP4DSBo" frameBorder="0" allowFullScreen></iframe>
      <br /><br />
      Responsibilities:
      <ul>
        <li>Develop features (e.g., product detail page and help screen)</li>
        <li>Assist the senior programmer in refactoring the app with Redux (a state managing library)</li>
        <li>Implement responsive web design for nice cross-platform experience</li>
        <li>Implement animations</li>
      </ul>
      (I'm a supporting programmer in this project. Other major team members include a project manager, another senior programmer and a UI designer)

      <br/><br />
      Major Skills:
      <br />
      React, Redux, webpack, ES6 (JavaScript), CSS, HTML, Pusher
    </p>
    <p>
      While e-commerce has revolutionized the way we shop, offline experience is still irreplacable in many circumstances. For instance, when you go purchasing clothes offline, you can feel the texture of the clothes and try them with different combinations to see whether they fit you well. And <i>Tofugear</i>, a startup based in Hong Kong, has been dedicated to utilizing information technologies to bring us a better off-line shopping experience.
    </p>
    <p>
      The whole solution is called <i>Omnitech</i>, with which the company was named as <i>Microsoft HK ISV Solution Partner of the Year</i>. In our solution, <strong>people</strong>, typically customers and store staff, and <strong>physical objects</strong> are connected by means of <strong>RFID technology</strong> and people, typically customers and store staff, can accomplish all kinds of tasks with the <strong>cross-platform (interactive kiosk, mobile phones, tablets) applications</strong> we provided. One of my favourite things about the solution how you can make payments a lot more effienct. In my country, when you want to check out in a clothing store, here's want you need to do: handing over all clothes he wants to a shop assistant for him to check information such as sizes and produce the bill; taking the bill to the checkout of the floor and wait in line; taking the receipt back to the store and obtain the products. With our solution, each time you bring a piece of clothing into the fitting room, it gets detected and when you wanted to check out, just added the ones you want to your virtual shopping cart with the smart mirror (interactive kiosk). Then, go through a pretty standard procedure of electronic payment and it's done.
    </p>
    <p>
      The <i>Omnitech</i> front end app, which I participated in developing, was implemented on interactive kiosks and smart tablets, acting as the main interface that interacts with customers. One of my task was to work closely with our UI desingers to implement animations, which we utilized as a major approach to make aware of changes in the application so as to provide better feedbacks to them about the interactions. For example, when a user clicked the "Add to Cart" button, the shopping cart component, which has a red badge indicating the number of in-room items, would be animated, drawing the user's attention to it informing that his action was valid. Sometimes, a user might not touch the button effectively and this would help them aware so as to retry. And I think compared to a popup or a new page displaying the result, which many other applications use, animation is a much less interceptive option in regard to user interaction.
    </p>
  </div>
)

export default Article3



