import React from "react"
import BlankLink from "./BlankLink"
import ArticlePreface from "./ArticlePreface"
import ArticleSection from "./ArticleSection"
import ArticleImage from "./ArticleImage"

const Article5 = () => (
  <div>
    <ArticleImage src="/images/5.gif" />
    <ArticlePreface>
      The application is hosted at: <a href="http://45.248.87.15/en-us/" target="_blank" rel="noopener noreferrer">http://45.248.87.15/en-us/</a>
      <br /><br />

      Responsibilities:
      <ul>
        <li>Designing the user interface</li>
        <li>Developing the application</li>
        <li>Provision a CentOS (Linux) server and take care of deployment as well as maintenance</li>
      </ul>

      <br/><br />
      Major Skills:
      <br />
      React, Redux, React Router, webpack, ES6 (JavaScript), GraphQL, PostgreSQL, Ruby, Rails, CentOS (Linux), Capistrano, CSS, HTML, Git
      <br/><br />
      Source Code: <a href="https://github.com/stanleysq/cahiers" target="_blank" rel="noopener noreferrer">https://github.com/stanleysq/cahiers</a>
    </ArticlePreface>

    <ArticleSection subhead="Objective">
      <p>
        Many film info websites are equipped with listing features which help people share information such as awards and personal favourites. However, I find such features fail to satisfy users' needs when the information is comparatively complex. For instance, every year I go through the nomination list of the Oscars as a reference for planning films to watch in the following year and this is how users of <i>Douban</i>, the biggest film info website in China, present related information:
      </p>
      <ArticleImage src="/images/5-1.png" />
      <p>
        Since users are not able to present hierarchical information, they can only structure the list by films instead of by awards such as the <i>Best Picture</i> and heavily rely on the remark section (the red area marked on the screenshot) to describe information. The remark can be hard to read when it goes lengthy and the whole list is just a mess where it is difficult to know what the competitors of each film are. In addition, the website only support one layout where each item occupy much space on the screen, which requires users to frequently scroll down and visit a new page in order view more items. I personally would like to have an extra compact mode where space is more effectively utilized
      </p>
      <p>
        Soon after I started to learn web development, I created a playground application to practice and experiment, which is, in my opinion, definately the best way to improve oneself in the domain. However, instead of following examples in tutorials, I decided to create one that attempted to address problems that I encountered. So, one of things I trying to do with my playground application is to enable users to present and view hierarchical information in a nice way.
      </p>
    </ArticleSection>

    <ArticleSection subhead="Design">
      <p>
        To begin with, I inspected typical user-created lists on the website and found that a hierarchy with 3 levels would enough for most users to present the information clearly and even join many existing lists into one. So, besides "list", I would have 2 additional levels which I called "division", which is contained by a "list", and "section", which is contained by a "division". If you want to present the <i>Oscars</i> nominations, you can create a section for each award to organize items (films, filmmakers or actors) under it and you can even create divisions for previous years for users to retrospect.
      </p>
      <ArticleImage src="/images/5-2.png" />
      <p>
        The wireframe above shows my basic blueprint for list page, in which hierarchical information can be presented clearly. Taking the <i>Oscars</i> for example, a user can switch between years by clicking on the buttons under the description. In each division, items are categorized by awards, which will be scrolled to when the user clicks on the corresponding link in the box on the right.
      </p>
    </ArticleSection>

    <ArticleSection subhead="Front-End Development">
      <p>
        The whole application is deveoped to be client-side heavy and has a relatively more independent front-end part compared to conventional web applications. But compared to other front-end applications I took part in developing, this time I have full freedom to experiment newer techniques.
      </p>
      <p>
        Besides back-end routing, I also implemented front-end routing with React Router. Without front-end routing, no matter how users interact with the web app, the URL doesnt change. This means that a user cannot use features like bookmarking (you can only bookmark the default page of the app) and navigate forward and backward in the browser history and when he refreshes the page, the web app is reset to the default page and he has to repeat some of his interactions to recover the previous state. But with front-end routing implemented in my app, URL changes in response to interactions. For example, you can bookmark the second division within a list as the URL changes to http://45.248.87.15/en-us/lists/the-index/divisions/2/ when you navigate to that division.
      </p>
    </ArticleSection>

    <p className="article-subhead">
      Back-End Development
    </p>
    <p>
      The back-end JSON API is developed with Ruby on Rails. Because I actually want to use the application, I decided to populate some real list data. There can be an array of data sources such as open databases, crawlers and manual inputs. So, I developed a unified interface for manipulating data. All the data comes from different sources should be inputted in or transformed to YAML, a data serialization language which is both friendly to human and computer, in specific formats which I designed. Essential models in the applications such as Person, Film and List include a module which knows how to interpret the YAML data, identify corresponding existing data in the database and make changes only when it's necessary. When the YAML data is ready, all I need to do is to run a single command in the terminal to process the YAML data and then populate or update the data in the application's database.
    </p>
    <p>
      Though based on Ruby on Rails, the JSON API might seem eccentric compared to a conventional Rails application as I implemented GraphQL, a cutting-edge data query language initially developed and later open sourced by Facebook. This is one of the GraphQL queries in my application which requests for all of current users starred lists:
    </p>
    <p className="article-code">
      query starredLists &#123;<br />
      &nbsp;&nbsp;starredLists &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;divisions &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;sections &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;items &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;... on ListItem &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;id<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;type<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;title<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;position<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;starred<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&#125;<br />
      &#125;
    </p>
    <p>
      As you may notice, the query looks like JSON with only keys. The GraphQL server usually has a single endpoint where you can send queries like this. And the result you get back, as expected, is JSON data with a very similar structure compared to your query, but this time with values attached.
    </p>
    <p>
      GraphQL can protentially facilitate better communication between applications by providing a better interface. Interacting with a conventional JSON API might involve multiple round trips. For example, if you want to get the data of a product and the store where it belongs from a REST API, you might need to send your request to "/products/56" first and then "/stores/23". What's worse, part of the data you get back from each endpoint might be superfluous. You might receive a whole bunch of product details which you don't want. Sometimes, these APIs try to address these issues by enable others to configure their requests with specific parameters, e.g. "with_store_info". However, the possibilities are still hard-coded and thus limited making the interface inflexible and usually difficult to manage and scale. More importantly, since it is the ones who communicate with the JSON API that have the needs for data, shouldn't they, rather than the API itself, decide what they should get? The new paradigm of GraphQL has reevaluated the roles of the server and clients and buttressed solutions which can be protentially better for both of the sides. With GraphQL, the responsibity of defining the structure of responses is largely shifted from the server to clients, which results in much greater flexibility on the client side, less development load on the server side and almost eliminates round trips between the two sides.
    </p>
    <p>
      Front-end web application, which I've been working on since the past 2 years, is one type of clients that GraphQL will make a significant impact on. When developing a complex application, we need frameworks like React to construct it based on reusable components. And we often knows what data each component depends on. For example, when developing a typical "comment" component, we know we need the content of the comment as well as the avatar URL and nickname of the user who posts it. Conventionally, the front-end side will need the back-end side to develop an specific API enpoint for this and what's worse, sometimes when the data dependency changes on the front-end side. However, if you are have GraphQL implemented, you can specify the data dependency for each component even within the same file and change it effortlessly when requirements change. In this way, the information about the data dependency is also reused, together with the component itself.
    </p>
  </div>
)

export default Article5



